@import "variables";

$sectionWidth : 10%;

.container {
  display: flex;
  flex-direction: row;

  @media screen and (min-width: $w-320) and (max-width: $w-1024) {
    flex-direction: column;
  }

  .section {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: $sectionWidth;
    background-color: $color-main-dark;

    .btn {
      margin: 1vw 0 0;
      padding: 0;
      transform: skewY(0);
    }
  }

  .sectionRibbon {
    position: relative;
    top: 0;
    left: -.5vw;
    width: .5vw;
    height: 100vh;
    background-color: $color-main;
    box-shadow: $main-shadow;
    z-index: 2;
  }

  .pdf {
    position: absolute;
    left: $sectionWidth;
    right: 0;
    width: 100% - $sectionWidth;
    height: 100%;

    @media screen and (min-width: $w-320) and (max-width: $w-1024) {
      width: 100%;
      height: 80vh;
      position: relative;
      left: 0;
      margin-top: 2rem;
    }
  }
}
