@import "variables";

.workContainer {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 8vw;

  @media screen and (min-width: $w-320) and (max-width: $w-1024) {
    width: 90%;
  }

  .workContainerHeader {
    display: flex;
    flex-direction: column;
    align-items: center;

    .headerBrief {
      width: 60%;
      margin: .5rem 0 1rem;
      text-align: center;
      color: $color-main-text-semi;
      font-size: 1.2rem;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        width: 100%;
        font-size: .9rem;
      }

      a {
        text-decoration: underline;
        color: $color-main-dark;
        &:hover {
          cursor: pointer;
          color: $color-main-text-hover;
        }
      }

    }

    .workCategory {
      display: flex;
      margin: 2rem 0 3rem;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        margin: 1rem 0 2rem;
      }

      span {
        font-size: 1rem;
        padding: .5rem 1.5rem;

        @media screen and (min-width: $w-320) and (max-width: $w-480) {
          font-size: .8rem;
        }

        &:hover {
          cursor: pointer;
        }
      }

      .left {
        border-top-left-radius: 10vw;
        border-bottom-left-radius: 10vw;
        border-right: 1px solid $color-main-dark !important;
        box-shadow: $main-shadow-btn;
      }

      .right {
        border-top-right-radius: 10vw;
        border-bottom-right-radius: 10vw;
        border-left: none !important;
        box-shadow: $main-shadow-btn;
      }

      .active {
        color: $color-main-dark;
        background-color: $color-main-light;
        border: 1px solid $color-main-dark;
      }

      .inActive {
        color: $color-main-text;
        border: 1px solid $color-main-text;

        &:hover {
          color: $color-main-text-semi;
        }
      }
    }
  }

  .itemsContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;

    .filtersContainer {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      .filterItem {
        padding: 4px 2rem;
        margin: .4rem 1rem;
        border-radius: 100px;
        background-color: $color-btn-sec;
        box-shadow: $main-shadow-btn;
        font-size: .8rem;
        transition: all 200ms;

        @media screen and (min-width: $w-320) and (max-width: $w-1024) {
          width: 6rem;
          margin: .4rem;
          display: flex;
          justify-content: center;
        }

        &:hover {
          cursor: pointer;
          color: $color-btn-text-sec;
          box-shadow: $main-shadow-btn-float;
          background-color: $color-btn-main;

          @media screen and (min-width: $w-320) and (max-width: $w-480) {
            box-shadow: $main-shadow-btn;
          }
        }

        &:active {
          box-shadow: $main-shadow-btn;
        }
      }
    }

    .filterItemActive {
      color: $color-btn-text-sec;
      background-color: $color-btn-main !important;
    }

    .prevListContainer {
      display: flex;
      flex: 1;
      flex-wrap: wrap;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        margin-top: 2rem;
      }
    }

    .workItemContainer {
      width: 33%;
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 1rem 0;
      animation: 1s ease-out 0s 1 fadeAnim;

      @media screen and (min-width: $w-480) and (max-width: $w-1024) {
        width: 50%;
      }

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        width: 100%;
      }

      .briefContainer {
        width: 70%;

        @media screen and (min-width: $w-480) and (max-width: $w-1024) {
          width: 84%;
        }

        @media screen and (min-width: $w-320) and (max-width: $w-480) {
          width: 100%;
        }

        .workTitleContainer {
          display: flex;
          align-items: center;
          margin: 0 0 .4rem;

          .featuredIcon {
            margin-right: 8px;
            color: $color-main;
          }

          .workTitle {
            font-size: 1.2rem;
            margin: 0;
          }

          .workSubTitle {
            color: $color-main-text-semi;
            margin-left: .2rem;
            font-size: 1rem;
          }

        }

        .briefTechs {
          display: flex;
          justify-content: flex-start;
          margin-top: .25rem;

          .briefTechsItem {
            padding: 4px 6%;
            margin-right: 2%;
            border-radius: 100px;
            background-color: $color-btn-sec;
            font-size: .65rem;
          }
        }

        .workBrief {
          font-size: .9rem;
          color: $color-main-text-semi;
          line-height: 1.3rem;
          height: 3.9rem;
          max-height: 3.9rem;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }

      .workItem {
        position: relative;
        width: 60%;
        height: 40%;
        margin: 4rem 4rem 3rem;
        border-radius: $border-radius;
        box-shadow: $main-shadow-card;
        transition: all 200ms;

        @media screen and (min-width: $w-320) and (max-width: $w-480) {
          width: 84%;
          height: 63%;
        }

        &:hover {
          cursor: pointer;
          transform: scale(1.1, 1.1);
          box-shadow: $main-shadow-card-float;
        }

        .clamp {
          overflow: hidden;
          width: 100%;
          height: 100%;
          border-radius: $border-radius;
          border: 0.1px solid #2222251A;

          .prevImg {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 200ms;

            &:hover {
              transform: scale(1.2, 1.2);
              transition: all 10s;
            }
          }
        }

        .prevFloatContainer {
          position: absolute;
          top: 0;
          width: 100%;
          height: 100%;
          pointer-events: none;
          animation: 1s ease-out 0s 1 fadeAnim;

          .prevImgFloat {
            pointer-events: auto;
            position: absolute;
            object-fit: cover;
            border-radius: $border-radius;
            box-shadow: $main-shadow-card;
            transition: all 1s;

            &:hover {
              cursor: pointer;
              transform: scale(1.2, 1.2);
              box-shadow: $main-shadow-card-float;
              transition: all 1s;
            }
          }

        }
      }
    }
  }
}

@keyframes fadeAnim {
  0% {
    transform: translateY(20%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
