@import "variables";
$browserHeight: 10%;

.title {
  margin: 4rem 0 0;
  font-size: 3rem;

  @media screen and (min-width: $w-320) and (max-width: $w-1024) {
    margin: 2rem 0 0;
    font-size: 2rem;
  }
}

.header {
  width: 100%;
  height: $header-height;
  display: flex;
  align-items: center;
  margin-top: -$app-bar-height * 2;
  padding-top: $app-bar-height * 2;
  background: $color-main-dark;
  background: linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(28, 28, 28, 1) 90%, rgba(28, 28, 28, 1) 100%);
  transform: skewY(-$skew);

  .headerRibbon {
    position: absolute;
    bottom: 0;
    height: 8px;
    width: 100%;
    background-color: $color-main;
    box-shadow: $main-shadow;
  }
}

.headerBtn {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: $app-bar-height / 2;
  margin-left: 2rem;
  transform: skewY($skew);
  padding: .5rem 1.5rem;
  border-radius: 4px;
  font-size: 1.2rem;
  color: $color-btn-text-sec;

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    margin-left: 0;
    margin-top: $app-bar-height-mob / 2;
    font-size: 1rem;
  }

  .headerIcon {
    font-size: 1rem;
    margin-right: .75rem;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      font-size: .8rem;
    }
  }

  &:hover {
    cursor: pointer;
    color: $color-main-text-hover;
  }

}

.footer {
  width: 100%;
  height: $footer-height;
  margin-top: auto;
  background-color: $color-main-dark;

  .footerRibbon {
    height: .4rem;
    width: 100%;
    background-color: $color-main;
    box-shadow: $main-shadow;
  }

  h5 {
    font-size: .8rem;
    display: flex;
    margin: 1rem 2rem 0;
    color: $color-main-text;

    @media screen and (min-width: $w-480) and (max-width: $w-1024) {
      margin: 1.8rem 2rem 0;
    }

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      margin: 1rem 12px 0;
      font-size: .7rem;
    }
  }

  a {
    font-size: .8rem;
    margin-left: .4rem;
    color: $color-main;
  }

}

.imageViewer {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;

  z-index: 999;

  .backdrop {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.9) 20%, rgba(4, 4, 4, 0.9) 40%, rgba(4, 4, 4, 0.9) 100%);
    z-index: -1;
  }

  .viewer {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 40%;
    background-color: #111;
    box-shadow: $main-shadow-card;

    img {
      height: 100%;
    }

    .btn {
      position: absolute;
      width: 3rem;
      height: 3rem;
      padding: 1rem;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(220, 220, 220, 0.4);
      box-shadow: $main-shadow-btn;
      border-radius: 50%;
      border: none;
      color: rgba(255, 255, 255, 0.1);

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        background-color: rgba(20, 20, 20, 0.4);
        color: rgba(255, 255, 255, 0.8);
      }

      &:hover {
        cursor: pointer;
        background-color: #555;
        color: rgba(255, 255, 255, 0.4);
        box-shadow: $main-shadow-btn-float;
      }
    }

    .left{
      left: 2rem;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        left: -4rem;
      }
    }

    .right {
      right: 2rem;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        right: -4rem;
      }
    }
  }

  .browser {
    width: 100rem;
    height: $browserHeight;
    display: flex;
    justify-content: center;

    img {
      height: 100%;
      margin: 0 1px;
      object-fit: contain;
      background-color: #222;
      transition: all 200ms;
      box-shadow: $main-shadow-card;

      &:hover {
        cursor: pointer;
        transform: scale(1.2);
        z-index: 1;
      }

    }

    .activeImg {
      transform: scale(1.2) !important;
      margin: 0 20px;
    }
  }
}

.customBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25%;
  padding: 1rem 0;
  margin-right: 1rem;
  border-radius: 4px;
  color: $color-btn-text-sec;
  background-color: $color-main;
  font-family: 'Roboto Slab', serif;
  font-size: 1rem;

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    width: 40%;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    width: 50%;
  }

  &:hover {
    cursor: pointer;
    background-color: $color-main-hover;
  }
}

.customBtnReverse {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25%;
  padding: 1rem 0;
  border-radius: 4px;
  color: $color-main;
  border: 1px solid $color-main;
  background-color: transparent;
  font-size: 1rem;
  font-family: 'Roboto Slab', serif;

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    width: 40%;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    width: 50%;
  }

  &:hover {
    cursor: pointer;
    background-color: $color-btn-text;
  }
}

.prevItemBtnContainer {
  display: flex;
  align-items: center;

  .prevBtn {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-right: 10%;
    padding: .6rem 1.5rem;
    border-radius: 4px;
    font-size: .84rem;
    color: $color-btn-text-sec;
    background-color: $color-main-dark;
    box-shadow: $main-shadow-btn;

    .prevIcon {
      margin-right: .75rem;
    }

    &:hover {
      cursor: pointer;
      color: $color-main-text-hover;
      box-shadow: $main-shadow-btn-float;
    }

    &:active {
      box-shadow: $main-shadow-btn;
    }

  }

  .urlBtn {
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 4px;
    font-size: .84rem;
    text-decoration: none;
    color: $color-main-dark;

    .prevIcon {
      margin-right: .25rem;
    }

    &:hover {
      cursor: pointer;
      color: $color-main-text-hover;
    }
  }

}

.navBtnContainer {
  position: relative;
  width: 60%;

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    width: 70%;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    width: 95%;
  }

  .btn {
    position: absolute;
    width: 2.4rem;
    height: 2.4rem;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ddd;
    box-shadow: $main-shadow-btn;
    border-radius: 50%;
    border: none;
    font-size: .8rem;
    color: #333;

    &:hover {
      cursor: pointer;
      background-color: #555;
      color: rgba(255, 255, 255, 0.4);
      box-shadow: $main-shadow-btn-float;
    }
  }

  .backBtn {
    top: 0;
    left: 1rem;

    @media screen and (min-width: $w-480) and (max-width: $w-1024) {
      top: 1rem;
    }

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      top: 1.4rem;
      left: 0;
      width: 1rem;
      height: 1rem;
    }
  }

  .nextBtn {
    top: 0;
    right: 1rem;

    @media screen and (min-width: $w-480) and (max-width: $w-1024) {
      top: 1rem;
    }

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      top: 1.4rem;
      right: 0;
      width: 1rem;
      height: 1rem;
    }
  }

  .disabled {

  }
}

.floatingBtn {
  position: fixed;
  bottom: .6rem;
  right: -6.4rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: $color-btn-text-sec;
  background-color: $color-main-dark;
  padding: .5rem;
  border-top-left-radius: 10rem;
  border-bottom-left-radius: 10rem;
  box-shadow: $main-shadow-btn;
  transition: right 200ms ease;
  z-index: 10;

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    bottom: .4rem;
    right: -6.4rem;
  }

  &:hover {
    cursor: pointer;
    right: 0;
    box-shadow: $main-shadow-btn-float;
  }

  .icon {
    width: 2rem;
  }

  span {
    width: 6rem;
    text-align: center;
  };

}
