@import "variables";

$imgSize: 220px;
$imgSizeMobile: 8rem;
$separatorSize: .4%;
$separatorMargin: 1%;
$shift: 24%;
$shift-tablet: 6%;
$card-width: 60%;
$card-width-tablet: 80%;
$card-width-mobile: 100%;

.wrongIdContainer {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;

  .title404 {
    font-size: 8rem;
    margin: 0;
    color: $color-main-text;
  }

  .title {
    font-size: 2rem;
    margin: 0;
    color: $color-main-text;
  }

}

.dataContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
  width: 70%;

  @media screen and (min-width: $w-1280) and (max-width: $w-1680) {
    width: 80%;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-1280) {
    width: 100%;
  }

  .headerInfo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .workBrief {
      width: 40%;
      margin: .5rem 0 1rem;
      text-align: center;
      color: $color-main-text-semi;
      font-size: 1rem;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        width: 75%;
        padding: 4px 1.2rem;
        font-size: .9rem;
      }
    }

    .techListContainer {
      width: 100%;

      .briefTechs {
        display: flex;
        justify-content: center;
        margin-top: .25rem;

        @media screen and (min-width: $w-320) and (max-width: $w-480) {
          flex-wrap: wrap;
        }

        .briefTechsItem {
          padding: 4px 1.6rem;
          margin: 0 .5%;
          border-radius: 100px;
          background-color: $color-btn-sec;
          font-size: .8rem;

          @media screen and (min-width: $w-320) and (max-width: $w-480) {
            margin: .2rem .5%;
            padding: 4px 1.2rem;
          }
        }
      }
    }
  }

  .techInfoContainer {
    display: flex;
    flex-direction: column;
    width: $card-width + $shift;
    margin: 2rem 0;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      width: 94%;
    }

    h2 {
      font-size: 1.6rem;
      margin-bottom: 0;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        font-size: 1.4rem;
      }
    }

    p {
      font-size: 1rem;
      margin-bottom: 0;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        font-size: .9rem;
      }
    }
  }
}

.marLeft {
  margin-left: $shift;
  animation: 1s ease-out 0s 1 fadeAnimLeft;

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    margin-left: $shift-tablet;
  }

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    margin-left: $shift-tablet;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    margin-left: 0;
  }

  .imgContainer {
    width: $imgSize * 4 / 3;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      width: auto;
    }
  }
}

.marRight {
  margin-right: $shift;
  animation: 1s ease-out 0s 1 fadeAnimRight;

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    margin-right: $shift-tablet;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    margin-right: 0;
  }

  .imgContainer {
    width: $imgSize * 4 / 3;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      width: auto;
    }
  }
}


.dataCardContainer {
  width: $card-width;
  display: flex;
  flex-direction: row;
  margin-top: 4rem;

  @media screen and (min-width: $w-480) and (max-width: $w-1024) {
    width: $card-width-tablet;
  }

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    width: $card-width-mobile;
  }

  h2 {
    margin: 0;
    text-align: center;
    font-size: 1.5rem;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      font-size: 1.2rem;
    }
  }

  p {
    font-size: .9rem;
    text-align: justify;
    text-align-last: center;
    margin: 1rem 2rem;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      margin: 1rem 1rem;
      text-align: start;
      text-align-last: start;
    }
  }

  img {
    object-fit: cover;
    overflow: hidden;
    border-radius: 4px;
    box-shadow: $main-shadow-data;
  }

  .imgH {
    height: $imgSize;
    max-width: $imgSize * 4 / 3;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      height: $imgSize / 1.4;
      max-width: $imgSize / 1.4;
    }
  }

  .imgV {
    width: $imgSize * .9;
    max-width: $imgSize * 4 / 3;
    //max-height: $imgSize;
    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      height: $imgSize * 1.2;
      max-width: $imgSize * .6;
    }
  }

  .separator {
    display: flex;
    width: $separatorSize;
    background-color: $color-main;
    margin: 0 $separatorMargin;
    border-radius: 100px;
    box-shadow: $main-shadow-data;
  }

  .dataTextContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;

    .subTitle {
      color: $color-main;
    }

  }
}

@keyframes fadeAnimRight {
  0% {
    transform: translateX(20%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeAnimLeft {
  0% {
    transform: translateX(-20%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
