@import "variables";

.ribbon {
  margin-top: -.1rem;
  height: 8px;
  width: 100%;
  background-color: $color-main;
  box-shadow: $main-shadow;
  z-index: 99;
  transform: skewY(-$skew);
}

.infoContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: -$app-bar-height * 2;
  padding-top: $app-bar-height * 2;
  background: $color-main-dark;
  background: linear-gradient(180deg, $color-bg-dark 0%, $color-bg-sec 90%, $color-bg-sec 100%);
  transform: skewY(-$skew);
  overflow: hidden;
  z-index: 99;

  .bgImg {
    position: absolute;
    bottom: 0;
    width: 100vw;
    object-fit: contain;
    overflow: hidden;
    opacity: .05;
  }

  .bg {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: $color-bg-main;
    opacity: .5;
    border-radius: 50%;
    padding: 4rem;
    box-shadow: $bg-shadow;
    z-index: 99;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      height: 40%;
      bottom: 4rem;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 100%;
      margin: 4rem;
      border-radius: 50%;
      background: linear-gradient(180deg, $color-bg-dark 0%, $color-bg-sec 90%, $color-bg-sec 100%);
      opacity: .5;
      transform: rotateZ(-10deg);
      box-shadow: $bg-shadow;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 100%;
      margin: 8rem;
      border-radius: 50%;
      background-color: #000;
      opacity: .5;
      transform: rotateZ(10deg);
      box-shadow: $bg-shadow;
    }
  }

  .info {
    width: 80%;
    margin: 6rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: skewY($skew);
    z-index: 100;

    @media screen and (min-width: $w-480) and (max-width: $w-1024) {
      width: 100%;
      margin: 5rem 0 2rem;
      padding: 0 12px;
    }

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      width: 100%;
      margin: 5rem 0 3rem;
      flex-direction: column;
      padding: 0 12px;
    }

    .img {
      width: 40rem;
      object-fit: cover;
      overflow: hidden;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        width: 18rem;
      }
    }

    .infoTextContainer {
      display: flex;
      flex-direction: column;

      .title {
        color: $color-main;
        font-size: 32px;

        @media screen and (min-width: $w-480) and (max-width: $w-1024) {
          font-size: 26px;
        }

        @media screen and (min-width: $w-320) and (max-width: $w-480) {
          font-size: 24px;
        }
      }

      .infoText {
        width: 70%;
        margin: 2rem 0;
        font-size: 1rem;
        color:$color-main-text;
        letter-spacing: 3px;
        //font-weight: bold;
        //background-image: linear-gradient(
        //                -30deg,
        //                #462523 0,
        //                #cb9b51 22%,
        //                #f6e27a 45%,
        //                #f6f2c0 50%,
        //                #f6e27a 55%,
        //                #cb9b51 78%,
        //                #462523 100%
        //);
        //color:transparent;
        //-webkit-background-clip:text;

        @media screen and (min-width: $w-320) and (max-width: $w-1024) {
          font-size: .9rem;
          margin: 1rem 0;
          width: 100%;
        }
      }

      .infoBtnContainer {
        display: flex;
        margin-top: 1rem;
      }

    }
  }
}

@keyframes fadeAnim {
  0% {
    transform: translateY(20%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
