@import "variables";

$border: 1px solid #ddd;
$padding: 3%;
$bg-color: #eee;

.container {
  display: flex;
  flex: 1;
  flex-direction: column;
  width: 40rem;
  align-items: center;
  justify-content: center;
  padding: 3rem 0 8rem;

  @media screen and (min-width: $w-320) and (max-width: $w-480) {
    width: 22rem;
  }

  input {
    height: .6rem;
    margin: 1% 0;
    padding: $padding;
    border-radius: .4rem;
    background-color: $bg-color;
    border: $border;
    outline-color: #c7c7c7;
    color: #222;
    font-size: 1rem;

    @media screen and (min-width: $w-320) and (max-width: $w-480) {
      height: 1.6rem;
    }
  }

  .msg {
    display: flex;
    flex: 1;
    width: 100% - $padding * 2;
    padding: $padding;
    resize: none;
    margin: .2rem 0 .4rem;
    border: $border;
    border-radius: .4rem;
    background-color: $bg-color;
    outline-color: #c7c7c7;
    color: #222;
    font-size: 1rem;
  }

  .subject {
    width: 100% - $padding * 2;
  }

  .row {
    display: flex;
    flex-direction: row;
    width: 100%;

    .name {
      width: 40%;
      margin-right: 2%;
    }

    .email {
      width: 100%;
    }

  }

  .contactBtnContainer {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: flex-start;

    .linkedInBtn {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: .75rem 2rem;
      border-radius: 4px;
      background-color: #0a66c2;
      font-family: 'Roboto Slab', serif;
      font-size: 1rem;
      color: $color-btn-text-sec;

      @media screen and (min-width: $w-320) and (max-width: $w-480) {
        width: 50%;
        padding: .75rem;
      }

      a {
        text-decoration: none;
        color: $color-btn-text-sec;
        margin-left: .75rem;
      }

      &:hover {
        cursor: pointer;
        background-color: #0856a6;
      }
    }
  }
}
