@import "variables";

.container {
  position: absolute;
  top: 0;
  height: $app-bar-height;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.item {
  margin: 0 20px;
  font-weight: 600;
  font-size: 1.2rem;
  color: $color-main-text;
  text-decoration: underline rgba(0, 0, 0, 0);
  text-underline-offset: 0.4em;
  text-decoration-thickness: 2px;
  transition: all 200ms;

  &:hover {
    cursor: pointer;
    color: $color-main-text-hover;
    text-decoration-color: $color-main-text-hover;
  }

}
